.face-wrapper {
  min-height: 100vh;
  background: #f4f4f4;
  display: flex;
  justify-content: center;
  align-items: center;
}

.face-card {
  width: 360px;
  padding: 24px;
  background: #ffffff;
  border-radius: 20px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.12);
  text-align: center;
}

.face-title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 12px;
}

.camera-frame {
  width: 260px;
  height: 330px;
  margin: 20px auto;
  background: #000;
  border-radius: 22px;
  overflow: hidden;
}

.camera-frame video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.face-instruction {
  font-size: 15px;
  font-weight: 500;
  margin-top: 12px;
}

.face-error {
  margin-top: 10px;
  font-size: 14px;
  color: red;
}
